* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-info>p {
 
display: flex;
flex-direction: row;
align-items: center;
justify-content: left;
}
.country-list {
  list-style: none;}

.country-list>li>p {
  display: flex;
flex-direction: row;
align-items: center;
justify-content: left;
gap: 10px;
}